import axios from 'axios'

const Post = ({ id }) => <h1>You are looking at post #{id}</h1>

Post.getInitialProps = async ({ query }) => {
  const res = await axios.get(
    'https://jsonplaceholder.typicode.com/comments?postId=4'
  )
  const { data } = res
  console.log(data)
  return query
}

export default Post
